/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto Mono', 'Courier New', monospace;
  background: #1c1e22; /* Dark background for contrast */
  color: #f6e9f4; /* Soft pinkish-white for text */
  line-height: 1.6;
}

/* Main Container */
.app-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  background: linear-gradient(145deg, #3a1f3d, #5b294d); /* Metallic pink gradient */
  border: 4px solid #d93e7d; /* Pink chrome border */
  border-radius: 12px;
  box-shadow: inset 0 0 15px #1e141a, 0 6px 12px #000;
  color: #fff;
  text-shadow: 1px 1px 1px #000;
}

/* Header */
h1 {
  text-align: center;
  color: #ff4fa1; /* Vibrant pink accent color */
  font-size: 2.4rem;
  letter-spacing: 3px;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  text-shadow: 0 0 10px #ff4fa1, 1px 1px 2px #000;
}

/* Input Group */
.input-group {
  margin-bottom: 1.2rem;
}

.input-group label {
  font-weight: bold;
  color: #f8cce1;
  display: block;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
}

.input-group input,
.input-group select {
  width: 100%;
  padding: 0.8rem;
  border: 2px solid #ff4fa1;
  background: #2e1c29;
  color: #ff94c9;
  font-family: 'Roboto Mono', monospace;
  font-size: 1rem;
  border-radius: 6px;
  text-transform: uppercase;
  box-shadow: inset 0 2px 3px #150c10;
  transition: all 0.3s ease;
}

.input-group input:focus,
.input-group select:focus {
  border-color: #ff94c9;
  outline: none;
  box-shadow: 0 0 8px #ff4fa1;
}

/* Position Inputs */
.positions-container {
  display: flex;
  gap: 0.6rem;
  flex-wrap: nowrap; /* Prevent wrapping to the next line */
  justify-content: center; /* Center align on small screens */
  overflow-x: auto; /* Add horizontal scrolling if necessary */
}

@media (max-width: 768px) {
  .positions-container {
    gap: 0.4rem; /* Slightly reduce gap for small screens */
  }

  .position-input {
    width: 40px; /* Adjust size for smaller screens */
    height: 40px;
    font-size: 1.2rem;
  }
}

.position-input {
  width: 50px;
  height: 50px;
  font-size: 1.5rem;
  text-align: center;
  color: #f6e9f4;
  border: 2px solid #ff4fa1;
  background: linear-gradient(145deg, #3a1f3d, #2e1c29);
  box-shadow: inset 0 0 6px #150c10, 0 2px 5px #000;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.position-input:focus {
  border-color: #ff94c9;
  box-shadow: 0 0 10px #ff4fa1;
}

.position-input.conflict {
  border-color: #ff1f3d;
  background: #3d1a20;
}

/* Error Message */
.error-message {
  color: #ff4f7f;
  background: #42151f;
  padding: 0.8rem;
  border: 2px solid #ff1f3d;
  margin-bottom: 1.2rem;
  text-align: center;
  border-radius: 6px;
  font-weight: bold;
  box-shadow: 0 0 6px #ff4fa1;
}

/* Buttons */
button {
  width: 100%;
  padding: 0.8rem;
  background: linear-gradient(145deg, #5b294d, #3a1f3d);
  color: #ff94c9;
  border: 2px solid #ff4fa1;
  font-size: 1rem;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 6px;
  box-shadow: 0 3px 5px #000;
  transition: all 0.3s ease;
}

button:hover {
  background: linear-gradient(145deg, #6a325b, #4a2443);
  border-color: #ff94c9;
  color: #fff;
  box-shadow: 0 6px 8px #000;
}

.clear-button {
  background: linear-gradient(145deg, #5e1c3c, #3b1227);
  color: #f6e9f4;
}

.clear-button:hover {
  background: #752145;
}

/* Results Section */
.results-container {
  margin-top: 2rem;
  border-top: 2px solid #ff4fa1;
  padding-top: 1rem;
}

.results-container h2 {
  font-size: 1.4rem;
  color: #ff4fa1;
  margin-bottom: 1rem;
  text-transform: uppercase;
  text-shadow: 0 0 6px #ff4fa1;
}

.results-list {
  list-style: none;
}

.result-item {
  background: linear-gradient(145deg, #4a2b40, #2f1827);
  color: #f8cce1;
  border: 2px solid #ff4fa1;
  padding: 0.8rem;
  margin-bottom: 0.5rem;
  border-radius: 6px;
  box-shadow: 0 2px 5px #000;
}

.result-item:hover {
  background: linear-gradient(145deg, #5b2e4d, #3b1f2f);
  color: #ff4fa1;
}

/* Responsive Design */
@media (max-width: 768px) {
  .positions-container {
    flex-wrap: wrap;
  }

  .position-input {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
}
